  white-space: nowrap;
  cursor: pointer;
  border: 1px solid transparent;
  text-align: left;
}

.button_primary {
  background-color: transparent;
  transition: color cubic-bezier(0.49, 0.09, 0.44, 0.78) 0.3s;
}

.button_primary:focus, .button_primary:hover {
