.word {
  position: absolute;
  bottom: 0;
  right: 50px;
  opacity: .05;
}

.word p {
  font-size: 360px;
  font-weight: 700;
  line-height: 1em;
}

.input-container {
  position: relative;
  display: block;
}

.input-container:not(:last-child) {
  margin-bottom: 20px;
}

.input-item {
  width: 100%;
  border: none;
  border-bottom: 1px #e9e9e9 solid;
  height: 50px;
  line-height: 50px;
  font-size: 14px;
  font-family: "Poppins", sans-serif;
  letter-spacing: .06em;
  background-color: transparent;
  transition: border cubic-bezier(0.49, 0.09, 0.44, 0.78) 0.3s;
  color: #E0B785;
}

.input-item:focus, .input-item:valid {
  border-bottom: 1px solid #E0B785;
}

.input-item:focus ~ .input-label, .input-item:valid ~ .input-label {
  font-size: 10px;
  top: -8px;
  color: #E0B785;
}

.input-label {
  position: absolute;
  font-family: "Poppins", sans-serif;
  transition: color cubic-bezier(0.49, 0.09, 0.44, 0.78) 0.3s;
  left: 0;
  top: 15px;
  font-size: 14px;
  transition: font-size .4s ease, top .4s ease;
  pointer-events: none;
}

.js-show {
  transform: translate(0, 0) rotate(0);
  opacity: 1;
  transition: transform 0.8s cubic-bezier(0.6, 0, 0.41, 1), opacity 0.8s cubic-bezier(0.6, 0, 0.41, 1);
}

.js-fadeIn {
  opacity: 0;
  transition: opacity 1s ease;
}

.js-slideRight {
  opacity: 0;
  transform: translate(50px, 0);
}

.js-slideLeft {
  opacity: 0;
  transform: translate(-50px, 0);
}

.js-show-onload {
  transform: translate(0, 0) rotate(0);
  opacity: 1;
  transition: transform 0.8s cubic-bezier(0.6, 0, 0.41, 1), opacity 0.8s cubic-bezier(0.6, 0, 0.41, 1);
}

@keyframes bg-animation {
  0% {
    background-position: left;
  }
  100% {
    background-position: right;
  }
}

.svg-icon {
  display: inline-block;
  width: 20px;
  height: 20px;
  fill: #e9e9e9;
  transition: fill 0.3s;
}

@media only screen and (min-width: 1024px), (any-hover: hover) {
  .svg-icon:hover {
    fill: #E0B785;
  }
}

.header {
  position: absolute;
  background-color: transparent;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
  padding: 30px 0;
}

.header-row {
  padding: 0 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  font-family: "AmesRoman", cursive, sans-serif;
  font-weight: 400;
  font-size: 60px;
  line-height: 0.8em;
  margin: 0;
  cursor: pointer;
}

.logo span {
  position: relative;
  font-weight: 400;
}

.logo span:first-child {
  color: #e9e9e9;
  -webkit-text-stroke: 1px #e9e9e9;
  -webkit-text-fill-color: #E0B785;
  display: inline-block;
  z-index: 10;
}

.logo span:last-child {
  margin-left: -.62em;
  color: #e9e9e9;
  -webkit-text-stroke: 1px #e9e9e9;
}

.humburger {
  background-color: transparent;
  width: 30px;
  height: 20px;
  position: relative;
  border: none;
  padding: 10px;
  z-index: 10;
  cursor: pointer;
  display: inline-block;
  margin-right: 10px;
}

@media only screen and (min-width: 1024px), (any-hover: hover) {
  .humburger:hover .humburger-item {
    background-color: #E0B785;
  }
}

.humburger.open .humburger-item {
  background-color: #E0B785;
}

.humburger.open .humburger-item:first-child, .humburger.open .humburger-item:last-child {
  width: 0;
  transition-property: width;
  transition-timing-function: cubic-bezier(0.49, 0.09, 0.44, 0.78);
  transition-duration: 0.3s;
}

.humburger.open .humburger-item:nth-child(2) {
  transform: rotate(45deg);
  width: 80%;
  transition-delay: .3s;
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.49, 0.09, 0.44, 0.78);
  transition-duration: 0.3s;
}

.humburger.open .humburger-item:nth-child(3) {
  transform: rotate(-45deg);
  width: 80%;
  transition-delay: .3s;
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.49, 0.09, 0.44, 0.78);
  transition-duration: 0.3s;
}

.humburger-item {
  width: 100%;
  height: 1px;
  background-color: #ffffff;
  position: absolute;
  border-radius: 3px;
}

.humburger-item:first-child {
  top: 0;
  left: 0;
  width: 75%;
  transition-property: width, background-color;
  transition-timing-function: cubic-bezier(0.49, 0.09, 0.44, 0.78);
  transition-duration: 0.3s;
}

.humburger-item:nth-child(2) {
  top: 50%;
  margin-top: -1px;
  left: 0;
  transition-property: width, background-color;
  transition-timing-function: cubic-bezier(0.49, 0.09, 0.44, 0.78);
  transition-duration: 0.3s;
}

.humburger-item:nth-child(3) {
  top: 50%;
  margin-top: -1px;
  left: 0;
  transition-property: width, background-color;
  transition-timing-function: cubic-bezier(0.49, 0.09, 0.44, 0.78);
  transition-duration: 0.3s;
}

.humburger-item:last-child {
  bottom: 0;
  right: 0;
  width: 75%;
  transition-property: width, background-color;
  transition-timing-function: cubic-bezier(0.49, 0.09, 0.44, 0.78);
  transition-duration: 0.3s;
}

.section-home {
