}

.abstract-lines {
  width: 200px;
  height: 300px;
  background-color: transparent;
  position: absolute;
  top: 160px;
  left: 400px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 0;
  pointer-events: none;
}

.abstract-lines span {
  width: 200px;
  height: 1px;
  background-color: #e9e9e9;
  display: block;
  transform: rotate(-48deg);
}

.abstract-lines span:nth-last-child(-n+3) {
