.scroll-btn {
  position: absolute;
  right: 0;
  bottom: 100px;
  font-size: 14px;
  transform: rotate(90deg);
  text-transform: uppercase;
  transition: color 0.3s cubic-bezier(0.49, 0.09, 0.44, 0.78);
  cursor: pointer;
  width: 100%;
  max-width: 140px;
}

@media only screen and (min-width: 1024px), (any-hover: hover) {
  .scroll-btn:hover {
    color: #E0B785;
  }
  .scroll-btn:hover::after {
    right: -10px;
  }
}

.scroll-btn:after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: 30px;
  height: 1px;
  background-color: #e9e9e9;
  transition: right 0.3s cubic-bezier(0.49, 0.09, 0.44, 0.78);
}

.scroll-btn .button-text {
