.about-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.about-img {
  width: 40%;
  border-radius: 50%;
}

.about-content {
  width: 60%;
  padding-right: 60px;
}

.section-title {
  margin-bottom: 60px;
}

.section-contact {
  background-image: linear-gradient(90deg, #111111 0%, #1f1f1f 100%);
  height: 100vh;
  background-size: 100%;
  background-repeat: no-repeat;
}

.section-contact .container_lg {
  height: 100%;
}

.section-contact .section-title {
  margin-bottom: 30px;
}

.section-contact .title-3 {
  text-align: center;
  color: #e9e9e9;
}

.contact-row {
  padding-top: 240px;
  display: flex;
  justify-content: space-between;
  height: 100%;
}

.contact-content {
  max-width: 50%;
  width: 50%;
  padding: 0 80px;
}

.contact-content p {
  text-align: justify;
  color: #e9e9e9;
}

.contact-form {
  max-width: 50%;
  width: 50%;
}

.form {
  position: relative;
  padding: 40px 60px 60px;
  margin-top: -40px;
  box-shadow: 0px 12px 43px -6px rgba(0, 0, 0, 0.75);
  max-width: 90%;
}

.form .button {
  display: block;
  text-align: center;
  margin: 40px auto 0;
  box-shadow: 0px 12px 43px -6px rgba(0, 0, 0, 0.75);
}
