// Ames Roman
@font-face {
  font-family: 'AmesRoman';
  src: url('../fonts/amesRoman/AmesRoman-Regular.eot');
  src: url('../fonts/amesRoman/AmesRoman-Regular.eot?#iefix') format('embedded-opentype'),
    url('../fonts/amesRoman/AmesRoman-Regular.svg#AmesRoman-Regular') format('svg'),
    url('../fonts/amesRoman/AmesRoman-Regular.ttf') format('truetype'),
    url('../fonts/amesRoman/AmesRoman-Regular.woff') format('woff'),
    url('../fonts/amesRoman/AmesRoman-Regular.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

